import React, { Component } from "react";
import Node from "./node";
import { dijkstra, getPath } from "../algorithms/dijkstra";

import "./pathfinder.css";

const START_NODE_ROW = 17;
const START_NODE_COLUMN = 10;
const FINISH_NODE_ROW = 17;
const FINISH_NODE_COLUMN = 34;

class Pathfinder extends Component {
	state = {
		grid: [],
		mouseDown: false,
		running: false,
	};

	componentDidMount() {
		const grid = createGrid();
		this.setState({ grid });
	}

	handleMouseDown(row, column) {
		if (!this.state.running) {
			const newGrid = toggleWall(this.state.grid, row, column);
			this.setState({ grid: newGrid, mouseDown: true });
		}
	}

	handleMouseEnter(row, column) {
		if (this.state.mouseDown) {
			const newGrid = toggleWall(this.state.grid, row, column);
			this.setState({ grid: newGrid });
		}
	}

	handleMouseUp() {
		this.setState({ mouseDown: false });
	}

	visualizeDijkstra() {
		const { grid } = this.state;
		const startNode = grid[START_NODE_ROW][START_NODE_COLUMN];
		const finishNode = grid[FINISH_NODE_ROW][FINISH_NODE_COLUMN];
		const visitedNodes = dijkstra(grid, startNode, finishNode);
		if (!isTrapped(visitedNodes)) {
			this.toggleRunning();
			this.clearPath();
			const path = getPath(finishNode);
			this.animateDijkstra(visitedNodes, path);
		}
	}

	animateDijkstra(visitedNodes, path) {
		const milliseconds = 5;
		for (let i = 0; i <= visitedNodes.length; ++i) {
			if (i === visitedNodes.length) {
				setTimeout(() => {
					this.animatePath(path);
				}, milliseconds * i);
				return;
			}
			setTimeout(() => {
				const node = visitedNodes[i];
				if (document.getElementById(`node-${node.row}-${node.column}`).className === "node")
					document.getElementById(`node-${node.row}-${node.column}`).className = "node node-visited";
			}, milliseconds * i);
		}
	}

	animatePath(path) {
		for (let i = 0; i < path.length; ++i) {
			setTimeout(() => {
				const node = path[i];
				if (document.getElementById(`node-${node.row}-${node.column}`).className === "node node-visited")
					document.getElementById(`node-${node.row}-${node.column}`).className = "node node-path";
				if (i + 1 === path.length) {
					this.toggleRunning();
				}
			}, 50 * i);
		}
	}

	toggleRunning() {
		if (this.state.running) {
			this.setState({ running: false });
			document.getElementById("start").disabled = false;
		} else {
			this.setState({ running: true });
			document.getElementById("start").disabled = true;
		}
	}

	clearPath() {
		const newGrid = this.state.grid;
		for (const row of newGrid) {
			for (let node of row) {
				if (
					document.getElementById(`node-${node.row}-${node.column}`).className === "node node-visited" ||
					document.getElementById(`node-${node.row}-${node.column}`).className === "node node-path"
				)
					document.getElementById(`node-${node.row}-${node.column}`).className = "node";
			}
		}
	}

	render() {
		const { grid, mouseDown } = this.state;
		return (
			<>
				<div className="grid">
					<button id="start" style={{ margin: "30px" }} onClick={() => this.visualizeDijkstra()}>
						START
					</button>
					{grid.map((row, rowIndex) => {
						return (
							<div key={rowIndex}>
								{row.map((node, nodeIndex) => {
									const { row, column, isStart, isFinish, isWall } = node;
									return (
										<Node
											key={nodeIndex}
											row={row}
											column={column}
											isStart={isStart}
											isFinish={isFinish}
											isWall={isWall}
											mouseDown={mouseDown}
											onMouseDown={(row, column) => this.handleMouseDown(row, column)}
											onMouseEnter={(row, column) => this.handleMouseEnter(row, column)}
											onMouseUp={() => this.handleMouseUp()}
										></Node>
									);
								})}
							</div>
						);
					})}
				</div>
			</>
		);
	}
}

const createGrid = () => {
	const grid = [];
	for (let row = 0; row < 25; ++row) {
		const currentRow = [];
		for (let column = 0; column < 40; ++column) {
			currentRow.push(createNode(row, column));
		}
		grid.push(currentRow);
	}
	return grid;
};

const createNode = (row, column) => {
	return {
		row,
		column,
		isStart: row === START_NODE_ROW && column === START_NODE_COLUMN,
		isFinish: row === FINISH_NODE_ROW && column === FINISH_NODE_COLUMN,
		isWall: false,
		isVisited: false,
		distance: Infinity,
		previousNode: null,
	};
};

const toggleWall = (grid, row, column) => {
	let node = grid[row][column];
	if (!node.isStart && !node.isFinish)
		node = {
			...node,
			isWall: !node.isWall,
		};
	grid[row][column] = node;
	return grid;
};

const isTrapped = (visitedNodes) => {
	for (let i = 0; i < visitedNodes.length; ++i) {
		if (visitedNodes[i].isFinish) {
			return false;
		}
	}
	alert("ERROR: START/FINISH NODE TRAPPED");
	return true;
};

export default Pathfinder;
